body,
html {
  background-color: #eeeeee;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
.clear:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clear {
  zoom: 1;
}
.pos-r {
  position: relative;
}
.hide {
  display: none;
}
a,
a:hover {
  text-decoration: none;
}
body,
html {
  background-color: #33C9FF;
}
.bjtop {
  position: relative;
  padding-top: 0.26666667rem;
  padding-bottom: 3.2rem;
  background: url('../../img/loginbj.png') bottom center no-repeat;
  background-size: cover;
}
.logo {
  width: 2.53333333rem;
  height: 0.66666667rem;
  background: url('../../img/logotop.png') center center no-repeat;
  background-size: cover;
  margin-left: 0.26666667rem;
}
.wen {
  width: 100%;
  font-size: 34px;
  color: #fff;
  text-align: center;
  padding-top: 1.06666667rem;
}
[data-dpr="2"] .wen {
  font-size: 68px;
}
[data-dpr="3"] .wen {
  font-size: 102px;
}
.logobai {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.46666667rem;
  background: url('../../img/logobai.png') bottom center no-repeat;
  background-size: cover;
}
.content {
  position: relative;
  padding: 0.8rem 0.48rem;
  background-color: #fff;
  margin: 0 0.26666667rem;
  border-top-left-radius: 0.26666667rem;
  border-top-right-radius: 0.26666667rem;
}
.content li {
  position: relative;
  margin-bottom: 0.4rem;
  display: flex;
}
.content li div {
  padding: 0.4rem;
  width: 100%;
  display: flex;
  background-color: #D0EDFF;
  border-radius: 0.13333333rem;
}
.content label {
  position: relative;
  width: 1.86666667rem;
  text-align: justify;
  margin-right: 0.26666667rem;
  font-size: 14px;
  color: #666;
  line-height: 0.53333333rem;
  height: 0.53333333rem;
}
[data-dpr="2"] .content label {
  font-size: 28px;
}
[data-dpr="3"] .content label {
  font-size: 42px;
}
.content label span {
  display: inline-block;
  /* Opera */
  padding-left: 100%;
}
.content label:after {
  position: absolute;
  top: 0;
  right: -0.13333333rem;
  content: ":";
}
.content input {
  flex: 1;
  border: none;
  background-color: inherit;
  line-height: 0.53333333rem;
  height: 0.53333333rem;
  padding-left: 0.26666667rem;
  font-size: 14px;
}
[data-dpr="2"] .content input {
  font-size: 28px;
}
[data-dpr="3"] .content input {
  font-size: 42px;
}
.content #phone {
  flex: none;
  width: 2.66666667rem;
}
.content .setphone div {
  width: 62%;
  padding-right: 0;
}
.content span.send {
  position: absolute;
  top: 0;
  right: 0;
  background: #09f;
  color: #fff;
  text-align: center;
  width: 30%;
  padding: 0.4rem 0;
  line-height: 0.53333333rem;
  font-size: 13px;
  border-radius: 0.13333333rem;
}
[data-dpr="2"] .content span.send {
  font-size: 26px;
}
[data-dpr="3"] .content span.send {
  font-size: 39px;
}
.content p {
  padding: 0.61333333rem 0;
  text-align: right;
  font-size: 12px;
  color: #666;
}
[data-dpr="2"] .content p {
  font-size: 24px;
}
[data-dpr="3"] .content p {
  font-size: 36px;
}
.content p i {
  width: 0.4rem;
  height: 0.4rem;
  background: url('../../img/ji.png') no-repeat;
  background-size: contain;
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.13333333rem;
}
.content .but {
  display: block;
  margin-top: 0.66666667rem;
  background-color: #09f;
  text-align: center;
  color: #fff;
  padding: 0.4rem;
  font-size: 16px;
  border-radius: 0.13333333rem;
}
[data-dpr="2"] .content .but {
  font-size: 32px;
}
[data-dpr="3"] .content .but {
  font-size: 48px;
}
.veson {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0.8rem 0;
  text-align: center;
  color: #666;
  font-size: 13px;
}
[data-dpr="2"] .veson {
  font-size: 26px;
}
[data-dpr="3"] .veson {
  font-size: 39px;
}
header {
  position: relative;
  padding: 0.4rem;
  background: linear-gradient(to bottom, #1BB0FC 0%, #25C3FA 100%);
}
header h2 {
  text-align: center;
  font-size: 16px;
  color: #fff;
}
[data-dpr="2"] header h2 {
  font-size: 32px;
}
[data-dpr="3"] header h2 {
  font-size: 48px;
}
header .arrow {
  position: absolute;
  top: 50%;
  left: 0.26666667rem;
  width: 0.26666667rem;
  height: 0.26666667rem;
  transform-origin: 50% 50%;
  transform: translateY(-50%) rotate(45deg);
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
}
.jihuo-content {
  background-color: #fff;
  padding: 0 0.26666667rem;
}
.jihuo-content li {
  padding: 0.4rem;
  position: relative;
  font-size: 14px;
  border-bottom: 1px solid #eee;
}
[data-dpr="2"] .jihuo-content li {
  font-size: 28px;
}
[data-dpr="3"] .jihuo-content li {
  font-size: 42px;
}
.jihuo-content label {
  display: inline-block;
  width: 1.46666667rem;
  text-align: justify;
  text-align-last: justify;
}
.jihuo-content input {
  display: inline-block;
  width: 4.66666667rem;
  border: none;
  margin-left: 0.26666667rem;
}
.jihuo-content .send {
  position: absolute;
  top: 50%;
  right: 1.33333333rem;
  width: 1.86666667rem;
  padding: 0.26666667rem;
  background-color: #09f;
  color: #fff;
  border-radius: 0.13333333rem;
  text-align: center;
  transform: translateY(-50%);
  font-size: 11px;
}
[data-dpr="2"] .jihuo-content .send {
  font-size: 22px;
}
[data-dpr="3"] .jihuo-content .send {
  font-size: 33px;
}
.jihuo-but {
  margin: 1.6rem;
  background-color: #09f;
  color: #fff;
  border-radius: 0.13333333rem;
  text-align: center;
  padding: 0.4rem;
  font-size: 16px;
}
[data-dpr="2"] .jihuo-but {
  font-size: 32px;
}
[data-dpr="3"] .jihuo-but {
  font-size: 48px;
}
.ji-success {
  padding: 0.26666667rem;
  border-bottom: 1px solid #eee;
  background-color: #fff;
  margin-bottom: 0.13333333rem;
}
.ji-success i {
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  background: url('../img/ji.png') top center no-repeat;
  background-size: cover;
  vertical-align: middle;
}
.ji-success span {
  padding-left: 0.26666667rem;
  color: #09f;
  vertical-align: middle;
}
.ji-psw {
  padding: 0 0.26666667rem;
  background-color: #fff;
}
.ji-psw li {
  padding: 0.4rem;
  position: relative;
  font-size: 14px;
  border-bottom: 1px solid #eee;
}
[data-dpr="2"] .ji-psw li {
  font-size: 28px;
}
[data-dpr="3"] .ji-psw li {
  font-size: 42px;
}
.ji-psw label {
  display: inline-block;
  width: 2.13333333rem;
}
.ji-psw input {
  display: inline-block;
  width: 4.66666667rem;
  border: none;
  margin-left: 0.26666667rem;
}
.ji-psw span {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0%, -50%);
  color: #f00;
}
